body {
  background: linear-gradient(90deg, #af67e9, #6565e7);
}

#centre-card {
  width: 327px;
  height: 537px;
  text-align: center;
  background: #fff url("bg-pattern-mobile.7e17b8b0.svg") 46px -1px no-repeat;
  border-radius: 20px;
  font-family: Kumbh Sans, sans-serif;
  position: absolute;
  top: calc(50% + 32px);
  left: 50%;
  transform: translate(-50%, -50%);
}

#centre-card h1 {
  color: #1d1e35;
  margin-top: 58px;
  font-weight: 700;
}

#illustration-mob {
  width: 237px;
  margin: -107px auto auto;
  display: flex;
}

#illustration-dt, #illustration-box-dt {
  display: none;
}

#question-area {
  margin-top: -7px;
}

.question {
  cursor: pointer;
  text-align: left;
  width: 280px;
  color: #4a4b5e;
  background-color: #fff;
  border: none;
  border-bottom: 1px solid #e7e7e9;
  margin: auto;
  padding: 17px 0 16px 1px;
  font-family: Kumbh Sans, sans-serif;
  font-size: 9.7pt;
  transition: all .2s;
  display: block;
  position: relative;
}

.question:hover {
  color: #f47c57;
}

.question:after {
  content: url("icon-arrow-down.bbd2655f.svg");
  width: 0;
  height: 0;
  background-color: #fff;
  display: block;
  position: absolute;
  top: 16px;
  right: 10px;
}

.answer {
  border-bottom: 1px solid #e7e7e9;
  margin: 11px 24px 0 25px;
  display: none;
}

.answer p {
  color: #787887;
  text-align: left;
  margin: auto auto 18px;
  font-size: 9pt;
  line-height: 18px;
}

.question-open {
  color: #1d1e35;
  border-bottom: none;
  padding-top: 18px;
  padding-bottom: 0;
  font-weight: 700;
}

.answer-open {
  display: block;
}

.question-open:after {
  top: 34px;
  right: 0;
  transform: rotate(180deg);
}

@media only screen and (min-width: 1020px) {
  #centre-card {
    width: 922px;
    height: 510px;
    text-align: right;
    background: #fff url("bg-pattern-desktop.96d2db15.svg") -577px -294px no-repeat;
  }

  #centre-card h1 {
    margin: 64px 376px 26px auto;
  }

  #illustration-mob {
    display: none;
  }

  .of-hidden {
    overflow: hidden;
  }

  #illustration-dt {
    margin: -7px auto auto -84px;
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  #illustration-box-dt {
    display: block;
    position: fixed;
    overflow: visible;
    transform: translate(-93px, 205px);
  }

  #question-area {
    margin-right: -377px;
  }

  .question {
    width: 352px;
    background-position-x: 328px;
    padding-left: 5px;
    font-size: 10.6pt;
  }

  .question:after {
    right: 24px;
  }

  .answer {
    width: 352px;
    margin-left: 475px;
  }

  .answer p {
    margin-left: 2px;
  }

  .question-open {
    padding-top: 15px;
  }

  .question-open:after {
    margin-right: -10px;
  }
}

.attribution {
  font-family: Kumbh Sans, sans-serif;
  font-size: 8pt;
  position: absolute;
  bottom: 0;
  right: 0;
}

